* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #0B2434;
  padding: 20px;
}

main {
    background-color: #F5F5F5;
    height: 400px;
    max-width: 800px;
    border-radius: 5px;
    margin: 40px auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.dice-container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 20px;
}

.dice-face {
  height: 50px;
  width: 50px;
  box-shadow: 0px 2px 2px rgba(0,0,0,0.15);
  border-radius: 10px;
  display: flex;
  justify-content:center;
  align-items:center;
  cursor: pointer;
  
}
.die-num {
  font-size: 1.5rem;
}
button {
   background-color: #5035ff;
   height: 50px;
   width: 100px;
   border: none;
   font-size: 1.2rem;
   color: white;
   border-radius: 6px;
 
   cursor: pointer;
}
button:focus {
  outline: none;
}
button:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0,0,0,0.7)
}
